import { request } from '../../request.js'
export default {
	data() {
		return {
			// 储存可视区域的高度
			statusHeight: '',
			// 储存折扣类型
			type: '',
			// 储存折扣度
			dynamics: '',
			// 储存数据
			dataList: [],
			// 储存接口返回的数据
			total_count: '',
			// 请求的页数(默认为1)
			page: 1,
			flag: true,
		}
	},
	onLoad(options) {
			if(!options.type) {
				for(let key in options) {
					this.type = key;
					this.dynamics = options[key];
					this.getGoodsList(this.type,this.dynamics,this.page)
				}
			}
			else {
				this.getGoodsList(undefined,undefined,this.page)
			}
			
		
		
		// 读取屏幕可视高度
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.windowHeight + 'px'
			}
		})
	},
	methods: {
		// 请求数据
		getGoodsList(type='',dynamics='',page) {
			uni.showToast({
				title: '数据加载中',
				icon: 'loading'
			});
			request({
				url: 'index.php?s=/wap/goods/discountGoods',
				method: 'post',
				data: {
					[type]: dynamics,
					page: page
				}
			}).then(res => {
				uni.hideToast();
				console.log(res);
				// 接口数据返回条数
				this.total_count = res.data.total_count;
				// 处理接口返回的图片
				let data = res.data.data;
				for(let i = 0;i < data.length;i++) {
					data[i].pic_cover_small = this.$api + data[i].pic_cover_small
				}
				this.dataList = [...this.dataList,...data];
			})
		},
		// 上拉加载更多
		loadMore() {
			if(this.flag) {
				console.log(111)
				this.page = this.page + 1;
				this.getGoodsList(this.type,this.dynamics,this.page);
			}
			if(this.dataList.length >= this.total_count) {
				console.log('false')
				this.flag = false
			}
		},
		// 跳转到商品详情页
		handleDetail(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		}
		
	}
}
